


.orderDetailsContainer {
    display: flex;
    box-sizing: border-box;
    flex-direction: column;
    justify-content: start;
    color: black;
    font-family: SHFont, sans-serif;
}



.orderDetailsIcon {
    width: 10px;
}

.textMd {
    font-size: 2.5rem;
}

.steakInfoContainer {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    padding: 0 5rem;
}

.steakInfoText {
    display: flex;
    box-sizing: border-box;
    flex-direction: column;
    flex-grow: 1;
    padding: 3rem;
    gap: 1.5rem;
}

.steakInfoText > * {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 2rem;
    justify-items: center;
}

.steakInfoText > * > img {
    width: 3.8rem;
}

.steakInfoText > * > p {
    margin: 0;
}


.headerContainer {
    display: flex;
    flex-direction: column;
    align-items: start;
    padding: 10rem 5rem;
    gap: 1rem;
}

.headerContainer > * {
    padding: 0;
    margin: 0;
}

.header {
    color: black;
    font-size: 6.5rem;
    font-weight: 300;
}

.steakIcon {
    display: flex;
    width: 15.2rem;
    height: 15.2rem;
    justify-content: center;
    border: 2px solid rgb(217 217 217);
    border-radius: 0.5rem;
    justify-items: center;
}

.steakIcon > img {
    width: 12.2rem;
    object-fit: contain;
}

.slide {
    display: flex;
    flex-grow: 1;
    padding: 0 5rem;
}

.slide > div {
    width: 100%;
    margin: auto;
}