

video {
    width: 300px;
}

.padH {
    box-sizing: border-box;
    padding: 0 5vh;
}

.textMd {
    font-size: 2.5rem;
    font-family: SHFont, sans-serif;
}

.gray {
    color: gray;
}

.orderStatusContainer {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    justify-content: space-between;
    align-items: center;
    padding-top: 7.5vh;
}

.hidden {
    display: none;
}

.headerContainer {
    padding: 0 5vh;
    box-sizing: border-box;
    width: 100%;
}

.headerContainer > *:nth-child(2) {
    color: gray;
}

.header {
    font-size: 6.5vh;
    font-weight: 300;
    color: black;
}

.abort {
    box-sizing: content-box;
    align-self: start;
    width: 17.9rem;
}

.qr {
    width: 12rem;
}
