.chooseChunkContainer {
    display: flex;
    flex-direction: column;
}

.chooseChunkContent {
    display: grid;
    height: 100%;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    justify-items: center;
    gap: 2.5rem;
}

.column {
    display: flex;
    flex-direction: column;
    gap: 4rem;
    width: 100%;
}

.leftColumn {
    align-items: end;
}

.rightColumn {
    align-items: start;
    justify-content: end;
    margin-bottom: 3rem;
    gap: 2.5rem;
}

.chunkButton {
    width: 22.5rem;
}

.header {
    display: flex;
    flex-direction: column;
    gap: 3vh;
    width: 85%;
    text-align: left;
    color: black;
}


.header div:first-child {
    font-size: 6.5vh;
}

.header div:last-child {
    font-size: 3.5vh;
}