nav {
    display: flex;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.navButton {
    position: relative;
    display: flex;
    width: 33%;
    height: 7rem;
    align-items: center;
    justify-content: center;
}

.navButton > img {
    height: 100%;
    object-fit: contain;
}

.bottomNavContainer {
    bottom: 0;
    display: flex;
    width: 100%;
    height: 11.5rem;
    box-sizing: border-box;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    background-color: white;

}

.bottomNavSeparator {
    width: 90%;
    height: 0.3vh;
    background-color: lightgrey;
}

.homeButton {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 15%;
    justify-content: flex-start;
    align-items: center;
    width: 33%;
    height: 7rem;
}

.homeButton img {
    height: 45%;
}

.homeButton span {
    font-size: 1.8rem;
    color: #919191;
}

.disabled {
    color: #E8E8E8!important;
}