


.heading {
    width: 100%;
    font-size: 8rem;
    text-align: left;
    padding-left: 10%;
}

.discoverPageContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.cards {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    justify-content: center;
    gap: 2.5rem;
    padding-top: 7rem;
}

.cards img {
    width: 90%;
}