

.chooseFatPercentContainer {
    display: flex;
    flex-direction: column;
}

.fatPercentCardContainer {
    display: grid;
    gap: 0.5vh;
    align-items: center;
    justify-content: center;
    grid-template-columns: 1fr 1fr 1fr;
    box-sizing: border-box;

}

.fatPercentCardContainer > * {
    width: 100%;
}

.content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 5vh 5vh 2vh;
    height: 100%;
}

.content > * {
    width: 100%;
}

.heading {
    width: 80%;
    line-height: 7vh;
}

.selectedChunkContainer {
    position: relative;
    display: flex;
}

.selectedChunkBackground {
    width: 100%;
}

.selectedChunkItems {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    height: 100%;
}

.selectedChunkText {
    position: absolute;
    bottom: 0;
    font-family: SHFont, sans-serif;
    padding-left: 5vh;
    color: white;
    font-size: 2.6vh;
}