

.enterPasswordContainer {
    z-index: 10;
    display: flex;
    box-sizing: border-box;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 7rem;
    background-color: black;
    gap: 5rem;
}


.enterPasswordMessage {
    color: white;
    font-size: 3rem;
    font-family: SHFont, sans-serif;
}

.enterPasswordInputField {
    width: 75%;
    height: 5rem;
    font-family: SHFont, sans-serif;
    font-size: 3rem;
    padding: 0rem 1rem;
}

.enterPasswordButton {
    width: 80%;
    height: 8rem;
    font-family: SHFont, sans-serif;
    font-size: 5rem;
}