.App {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  color: black;
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  background-color: white;
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@font-face {
	font-family: SHFont;
    font-weight: bold;
	src: local("SHFont"),url('./fonts/GalanoTest-Medium.otf') format("opentype");
}

@font-face {
	font-family: SHFontLight;
	src: local("SHFontLight"),url('./fonts/GalanoTest-Light.otf') format("opentype");
}

